html{
    width: 100%;
    height: 100%;
}
body{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    border: 0;
    font-style: inherit;
    text-align: left;
    vertical-align: baseline;
    font-family: $latoRegular;
}
*{
    box-sizing: border-box;
}
a {
    text-decoration: none;
    &:hover, &:focus, &:active{
        text-decoration: none;
    }
}
main {
    min-height: calc(100vh - 430px)
}
input{
    outline: none;
    &:hover, &:focus, &:active{
        outline: none;
    }
}

textarea{
    outline: none;
    &:hover, &:focus, &:active{
        outline: none;
    }
}

%clearfix {
    *zoom: 1;
    &:after {
        content: '';
        display: table;
        line-height: 0;
        clear: both;
    }
}
.fw-page-builder-content {
    display: flex;
    min-height: calc(100vh - 430px);
    flex-direction: column;
    justify-content: space-between;
}